<template>
  <div class="p5Spring">
    <Header heading="Processing JS" />
    <P5NavBar />
    <h3>Click and drag to interact!</h3>
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import P5NavBar from "@/components/P5NavBar.vue";

export default {
  components: {
    Header,
    P5NavBar
  },
  data() {
    return {
      script: null,
      ps: null,
      canvas: null,
      bob: null,
      spring: null
    };
  },
  mounted() {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight);
        this.canvas.parent(this.$refs.canvas);
        p.frameRate(30);

        this.spring = new Spring(p.width / 2, p.height / 2, 100);
        this.bob = new Bob(p.width / 2, p.height / 2 + 50);
      };

      p.draw = () => {
        p.background(255);

        let gravity = p.createVector(0, 2);
        this.bob.applyForce(gravity);

        this.spring.connect(this.bob);
        this.spring.constrainLength(this.bob, 30, 200);

        this.bob.update();

        this.spring.displayLine(this.bob);
        this.bob.display();
        this.spring.display();
      };

      class Spring {
        constructor(x, y, l) {
          this.anchor = p.createVector(x, y);
          this.len = l;
          this.k = 0.1;
        }

        connect(b) {
          // vector pointing from anchor to bob location
          let force = P5.Vector.sub(b.location, this.anchor);

          let d = force.mag();
          let stretch = d - this.len;

          force.normalize();
          force.mult(-1 * this.k * stretch);

          b.applyForce(force);
        }

        display() {
          p.fill("#395169");
          p.rectMode(p.CENTER);
          p.rect(this.anchor.x, this.anchor.y, 10, 10);
        }

        displayLine(b) {
          p.stroke("#395169");
          p.line(b.location.x, b.location.y, this.anchor.x, this.anchor.y);
        }

        constrainLength(b, minLength, maxLength) {
          let dir = P5.Vector.sub(b.location, this.anchor);
          let d = dir.mag();

          // Is it too short?
          if (d < minLength) {
            dir.normalize();
            dir.mult(minLength);

            // Reset location and stop from moving (not realistic physics)
            b.location = P5.Vector.add(this.anchor, dir);
            b.velocity.mult(0);
          }
          // Is it too long?
          else if (d > maxLength) {
            dir.normalize();
            dir.mult(maxLength);

            // Reset location and stop from moving (not realistic physics)
            b.location = P5.Vector.add(this.anchor, dir);
            b.velocity.mult(0);
          }
        }
      }

      class Bob {
        constructor(x, y) {
          this.location = p.createVector(x, y);
          this.velocity = p.createVector();
          this.acceleration = p.createVector();
          this.mass = 24;
          this.damping = 0.98;

          // For user interaction
          this.dragOffset = p.createVector();
          this.dragging = false;
        }

        update() {
          this.velocity.add(this.acceleration);
          this.velocity.mult(this.damping);
          this.location.add(this.velocity);
          this.acceleration.mult(0);
        }

        applyForce(force) {
          let f = force.copy();
          f.div(this.mass);
          this.acceleration.add(f);
        }

        display() {
          p.stroke("#395169");
          p.strokeWeight(2);
          p.fill("#42b983");

          if (this.dragging) {
            p.fill("#1A4A34");
          }

          p.ellipse(
            this.location.x,
            this.location.y,
            this.mass * 2,
            this.mass * 2
          );
        }

        handleClick(mx, my) {
          let d = p.dist(mx, my, this.location.x, this.location.y);
          if (d < this.mass) {
            this.dragging = true;
            this.dragOffset.x = this.location.x - mx;
            this.dragOffset.y = this.location.y - my;
          }
        }

        stopDragging() {
          this.dragging = false;
        }

        handleDrag(mx, my) {
          if (this.dragging) {
            this.location.x = mx + this.dragOffset.x;
            this.location.y = my + this.dragOffset.y;
          }
        }
      }

      p.mousePressed = () => {
        this.bob.handleClick(p.mouseX, p.mouseY);
      };

      p.mouseReleased = () => {
        this.bob.stopDragging();
      };

      p.mouseDragged = () => {
        this.bob.handleDrag(p.mouseX, p.mouseY);
      };

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight);
      };
    };
    const P5 = require("p5");
    this.ps = new P5(this.script);
    // eslint-disable-next-line
    console.log(this.ps);
  }
};
</script>

<style lang="scss" scoped>
.canvasP5 {
  display: block;
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}
.p5Spring {
  user-select: none;
}
</style>
